body {
  font: 25px "Century Gothic", Futura, sans-serif;
  font-style: italic;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square{
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 40px;
  width: 45px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
}

.resetter
{
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 55px;
  width: 133px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
}

.square:focus {
  outline: none;
  border: 2px solid rgb(0, 0, 0);
}

.square:hover{
  background: rgb(105, 105, 105);
}

.resetter:focus {
  outline: none;
  border: 2px solid rgb(0, 0, 0);
}

.resetter:hover{
  background: rgb(105, 105, 105);
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
